@import '_fonts.scss';

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
    display: block;
}
* {
    box-sizing: border-box;
    outline: 0;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
body {
    line-height: 1;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI",'SFUIDisplay', Helvetica, Arial, sans-serif;
    font-size: 18px;
    background: #fdfdfd;
    user-select: none;
}
ol, ul {
    list-style: none;
}
blockquote, q {
    quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
    content: '';
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}
input {
    -webkit-appearance: none;
}
a {
    text-decoration: none;
}
img {
    max-width: 100%;
}
main {
  overflow: hidden;
}
.app {
    max-width: 1360px;
    margin: 0 auto;
}
.header {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    max-width: 1360px;
    height: 55px;
    margin: 0 auto;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background: #fff;
    box-shadow: 0 7px 100px -10px rgba(0,0,0,.75);
    padding: 0 8.12em;

    @media (min-width: 992px) and (max-width: 1279px) {
        padding: 0 1em;
    }
}
.nav {
    display: flex;
    flex: 2 1;
    justify-content: space-around;

    &__item {
        text-transform: uppercase;
        font-weight: 300;
        font-size: 14px;
        color: #0a0a0a;
    }
}
.logo {
    display: flex;
    flex: 1 1;
    justify-content: center;
    position: relative;
    top: 40%;
    padding: 0px 5%;

    @media (min-width: 992px) and (max-width: 1279px) {
        padding: 0 0;
    }
}
.communications {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex: 2 1;
}
.calendar {

    &__item {
        font-size: 14px;
        color: #65943f;
        text-transform: uppercase;
        font-weight: 300;
    }
}
.phone {

    &__item {
        font-size: 14px;
        color: #d78296;
        text-transform: uppercase;
        font-weight: 300;
    }
}
.section {

    &_intro {
        background: url('../images/topBG.jpg') no-repeat;
        background-size: 100% 125%;
        height: 585px;
        margin-top: 55px;
    }
}
.social-net {
    display: flex;
    justify-content: space-between;
}
.instagram {
    margin-right: 0.44em;
}
.facebook {
    margin-right: 0.44em;
}
.hidden {
    display: none;
}
